import React, { useState } from 'react';
import moment from 'moment';
import { FormFeedback,Col,Button, Modal, ModalHeader, ModalBody, ModalFooter,Form,Label, Input, FormGroup } from 'reactstrap';

const TaskModal = (props) => {

    const {
            btnLabel,
            btnColor,
            btnClass,
        } = props;

    const [modal, setModal] = useState(false);
    const [firstCheckErr,setFCheckModal] = useState(false);
    const [cat ,setCat] = useState("");
    const [text ,setText] = useState("");

    const toggle = () => setModal(!modal);

    const addItemToggle = () => {
      if(props.curText.trim().length>0 && props.curCat.trim().length>0){
        props.addItem();
        setModal(!modal);
        setCat("");
        setText("");
        setFCheckModal(false);
      }else{
        //props.errCheck();
        setFCheckModal(true);
      }
    }

    const isValidCat =()=>{
      console.log(firstCheckErr);
      if(firstCheckErr){
        if(cat.trim().length<1){
          return true;
        }else{
          return false;
        }
      }else{
        return false;
      }
    }
    const isValidTask =()=>{
      if(firstCheckErr){
        if(text.trim().length<1){
          return true;
        }else{
          return false;
        }
      }else{
        return false;
      }
    }

    const getItemsAuto= props.tagList.map((option)=>{
       var list =[...{label:option.tag}];
       return list
    });

    const onChangeCat = (value)=>{
      setCat(value);
      props.changeCat(value);
    }
    const onChangeText = (value)=>{
      setText(value);
      props.changeText(value);
    }
    const cancelToggle = () => {
      setFCheckModal(false);
      setCat("");
      setText("");
      props.cancelItem();
      setModal(!modal);
  }
    const editItemToggle = (props.mode==="Edit")?
        () => {
            props.editItem();
            setModal(!modal);
        } : toggle;

  return (
    <div>
      <Button className={btnClass} color={btnColor} onClick={editItemToggle}>{btnLabel}</Button>
      <Modal isOpen={modal} toggle={toggle}>
    <ModalHeader toggle={cancelToggle}>{props.mode} a task</ModalHeader>
        <ModalBody>
            <Form >
            <FormGroup>
                <Label for="cat" >* Category: </Label>
                <Input type="select" invalid={isValidCat()} onChange={(e)=>onChangeCat(e.target.value)}
                  value={props.curCat}
                  className="form-control mb-2 mr-sm-2" name="cat" id="cat"  placeholder="Put category here" >
                    {props.tagList.map((option)=>(
                      <option value={option.tag}>{option.tag}</option>
                    ))}
                </Input>

                <FormFeedback invalid>Oops! The task category is needed</FormFeedback>
            </FormGroup>
              <FormGroup>
                <Label for="name" >* Task: </Label>
                <Input invalid={isValidTask()} onChange={(e)=>onChangeText(e.target.value)}
                    value={props.curText}
                    className="form-control mb-2 mr-sm-2" name="name" id="name"  placeholder="Put your task here" />
                    <FormFeedback invalid>Oops! The task name is needed</FormFeedback>
              </FormGroup>

              <FormGroup tag="fieldset" row>
                <legend className="col-form-label col-sm-2">Priority: </legend>
                  <Col sm={10}>
                    <FormGroup check>
                      <Label check>
                        <Input type="radio" onChange={(event)=>props.changePriority(event.target.value)}
                          value="1"
                          className="mb-2 mr-sm-2" name="priority" id="priority1"
                          checked={props.curPriority==="1"}
                          />
                          1 - Most Important
                        </Label>
                    </FormGroup>
                    <FormGroup check>
                      <Label check>
                        <Input type="radio" onChange={(event)=>props.changePriority(event.target.value)}
                          value="2"
                          className="mb-2 mr-sm-2" name="priority" id="priority2"
                          checked={props.curPriority==="2"}/>
                          2 - Important
                      </Label>
                    </FormGroup>
                    <FormGroup check>
                      <Label check >
                        <Input type="radio" onChange={(event)=>props.changePriority(event.target.value)}
                          value="3"
                          className="mb-2 mr-sm-2" name="priority" id="priority3"
                          checked={props.curPriority==="3"}/>
                          3 - Least Important
                      </Label>
                    </FormGroup>
                  </Col>
              </FormGroup>

              <FormGroup>
                <Label for="deadline" >Deadline: </Label>
                <Input type="date" onChange={(event)=>props.changeDeadline(event.target.value)}
                    value={props.curDeadline}
                    className="form-control mb-2 mr-sm-2" name="deadline" id="deadline" min={moment().format("YYYY-MM-DD")}/>
              </FormGroup>
              <FormGroup>
                <Label for="desc" >Description: </Label>
                <textarea onChange={(event)=>props.changeDesc(event.target.value)}
                    value={props.curDesc}
                    className="form-control mb-2 mr-sm-2" name="desc" id="desc"  placeholder="Put the description here" />
              </FormGroup>
            </Form>
        </ModalBody>
        <ModalFooter>
          <Button color="primary" onClick={addItemToggle}>{props.mode}</Button>{' '}
          <Button color="secondary" onClick={cancelToggle}>Cancel</Button>
        </ModalFooter>
      </Modal>
    </div>
  );
}

export default TaskModal;



