import React,{Component} from 'react';
import TaskModal from './TaskModal';
import { UncontrolledTooltip,UncontrolledCollapse,Badge, Card, ListGroup, ListGroupItem,Button, Form, CardBody, CardTitle} from 'reactstrap';

class Item extends Component{
    constructor(props){
        super(props);
        this.state={
            items:[{
              text: "Deliver 4HC3 assignment-2 next week",
              cat: "Assignment",
              desc:"Design a voice-user interface",
              priority:1,
              deadline:"2021-06-21",
              key:1
            },
            {
              text: "Deliver 4AA3 assignmen-3 next week",
              cat: "Assignment",
              desc:"Design a Component graph",
              priority:2,
              deadline:"2021-06-23",
              key:2
            },
            {
              text: "4G03 midterm this week",
              cat: "Test",
              desc:"no calculator allowed",
              priority:1,
              deadline:"2021-06-17",
              key:3
            }], nextID: 3, editIndex: 0, curText: "", curCat:"",curDeadline:"",
            curDesc:"",curPriority:"Unset", tags:[{
                tag:"Assignment",
                count:2,
                key: 2
            },{
                tag:"Test",
                count:1,
                key: 3
            }],editTagIndex:0,nextTagID: 3,mode:"Add"

        }
        this.addItem = this.addItem.bind(this);

    }
    addItem(){
        if(this.state.curText!=="" && this.state.curCat!==""){
            if(this.state.mode === "Add"){
                var newItem = {
                    text: this.state.curText,
                    cat: this.state.curCat,
                    desc: this.state.curDesc,
                    priority:this.state.curPriority,
                    deadline: this.state.curDeadline,
                    key: this.state.nextID + 1
                }
                this.setState((prevState) => {
                    return {
                        nextID: this.state.nextID + 1,
                        curText: "",
                        curCat:"",
                        curDeadline:"",
                        curPriority:"Unset",
                        curDesc:"",
                        items: prevState.items.concat(newItem) ,
                    };
                });
            }else{
                var newItems = this.state.items;
                if(newItems[this.state.editIndex].cat !== this.state.curCat){
                    this.state.tags[this.state.editTagIndex].count-=1;
                    if(this.state.tags[this.state.editTagIndex].count===0){
                        this.setState({
                            tags: this.state.tags.filter( (ele) => ele.tag !== newItems[this.state.editIndex].cat)
                        })
                    }
                }
                newItems[this.state.editIndex].text = this.state.curText;
                newItems[this.state.editIndex].cat = this.state.curCat;
                newItems[this.state.editIndex].deadline = this.state.curDeadline;
                newItems[this.state.editIndex].priority = this.state.curPriority;
                newItems[this.state.editIndex].desc = this.state.curDesc;

                this.setState({
                    mode: "Add",
                    curText: "",
                    curCat:"",
                    curDeadline:"",
                    curDesc:"",
                    curPriority:"Unset",
                    items: newItems
                })
            }
            //Keep track of tags of the task
            if(!(this.state.tags.filter((ele)=>ele.tag===this.state.curCat).length>0)){
                var newTag ={
                    tag:this.state.curCat,
                    count:1,
                    key: this.state.nextTagID+1
                }
                this.setState((prevState)=>{
                    return{
                        nextTagID: this.state.nextTagID + 1,
                        tags:prevState.tags.concat(newTag)
                    };
                });
            }else{
                this.state.tags[this.state.tags.findIndex((item)=>item.tag===this.state.curCat)].count+=1;
            }
        }
    }
    cancelItem(){
        this.setState({
            mode: "Add",
            curText: "",
            curCat:"",
            curDeadline:"",
            curDesc:"",
            curPriority:"Unset"
        })
    }
    delete(key){
        var dummy = this.state.items.find((item)=>item.key===key);
        var deleteIndex = this.state.tags.findIndex((item)=>item.tag===dummy.cat);
        this.state.tags[deleteIndex].count-=1;
        if(this.state.tags[deleteIndex].count===0){
            this.setState({
                tags: this.state.tags.filter( (ele) => ele.tag !== dummy.cat)
            })
        }
        this.setState({
            items: this.state.items.filter( (item) => item.key !== key)
        })
    }
    change(key){
        var editItem = this.state.items.find((item)=>item.key ===key);
        this.setState({
            curText:editItem.text,
            curCat: editItem.cat,
            curDeadline: editItem.deadline,
            curDesc: editItem.desc,
            curPriority:editItem.priority,
            mode: "Edit",
            editIndex: this.state.items.indexOf(editItem),
            editTagIndex:this.state.tags.findIndex((item)=>item.tag===editItem.cat)
        })
    }
    errCheck(){

    }

    render(){

        var listItems = this.state.tags.map((tagItem)=>{
            var groupByTag = this.state.items.filter((item)=>item.cat===tagItem.tag);
            return(
                <ListGroup key={"ListGroup"+tagItem.key} className="col-6 col-md-4" >
                <div>
                <ListGroupItem key={tagItem.key} color="success">Category: {tagItem.tag}</ListGroupItem>
                {groupByTag.sort((a, b) => a.priority > b.priority ? 1 : -1).map((item)=>{
                    return(
                        <ListGroupItem key={item.key}>
                            <Card >
                                <CardTitle tag="h5" className="ml-2 mt-2" name="name" id={"name"+item.key}><span id={"title"+item.key}><Badge color="info">Task:</Badge> {item.text}</span></CardTitle>
                                <UncontrolledTooltip placement="right" target={"#title"+item.key}>
                                    Click to view in detail
                                </UncontrolledTooltip>
                                <UncontrolledCollapse toggler={"#name"+item.key}>
                                    <CardBody>
                                        <ul className="list-group mb-2">
                                            <li name="priority" id={"priority"+item.key}>Priority: {"P"+item.priority}</li>
                                            <li  name="deadline" id={"deadline"+item.key}>Deadline: {item.deadline}</li>
                                            <li  name="desc" id={"desc"+item.key}>Description: {item.desc}</li>
                                        </ul>
                                    <Form inline>
                                        <TaskModal changeText={(value) => this.setState({curText: value})}
                                            changeCat={(value) => this.setState({curCat: value})}
                                            changeDeadline={(value) => this.setState({curDeadline: value})}
                                            changeDesc={(value) => this.setState({curDesc: value})}
                                            changePriority={(value) => this.setState({curPriority: value})}
                                            mode="Edit"
                                            curText={this.state.curText}
                                            curCat={this.state.curCat}
                                            curDeadline={this.state.curDeadline}
                                            curPriority={this.state.curPriority}
                                            curDesc={this.state.curDesc}
                                            editItem={()=>this.change(item.key)}
                                            addItem={()=>this.addItem()}
                                            errCheck={()=>this.errCheck()}
                                            cancelItem={()=>this.cancelItem()}
                                            tagList = {this.state.tags}
                                            btnLabel="Edit"
                                            btnColor="primary"
                                            btnClass="mb-2 mr-1"
                                            />
                                        <Button onClick={()=>this.delete(item.key)} color="secondary" className="mb-2">Delete</Button>
                                        </Form>
                                    </CardBody>
                                </UncontrolledCollapse>
                            </Card>
                        </ListGroupItem>
                    );
                })}
                </div></ListGroup>
            )
        });

        return(
            <div className="container">
                <div className="row row-content">
                    <TaskModal changeText={(value) => this.setState({curText: value})}
                    changeCat={(value) => this.setState({curCat: value})}
                    changeDeadline={(value) => this.setState({curDeadline: value})}
                    changeDesc={(value) => this.setState({curDesc: value})}
                    changePriority={(value) => this.setState({curPriority: value})}
                    mode="Add"
                    curText={this.state.curText}
                    curCat={this.state.curCat}
                    curDeadline={this.state.curDeadline}
                    curDesc={this.state.curDesc}
                    curPriority={this.state.curPriority}
                    addItem={()=>this.addItem()}
                    errCheck={()=>this.errCheck()}
                    tagList = {this.state.tags}
                    cancelItem={()=>this.cancelItem()}
                    btnLabel="+ Add Task"
                    btnColor="danger"
                    />
                </div>
                <div className="row row-content">
                        {listItems}
                </div>
            </div>
        );
}}


export default Item;



