import React, { Component } from 'react';
import {  Jumbotron } from 'reactstrap';

class Header extends Component {
  render() {
    return(
      <Jumbotron>
           <div className="container">
               <div className="row row-header">
                   <div className="col-12 col-sm-12">
                       <h1>Task Tracker</h1>
                    </div>
               </div>
           </div>
       </Jumbotron>
    );
  }
}

export default Header;


